@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');
/* base styles */
* {
  margin-top: 0px;
  margin: 0px;
  padding-top: 0px;
  font-family: "Quicksand";
  color: #333;
  /*font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;*/
}
.wrapper
{
  display:grid;
  grid-template-rows: repeat(4, [row] auto  );
  grid-template-columns: auto;
  display: inline-grid;
  border: #272727 2px ;
}
table {
  display: table; 
  border-collapse: collapse;
  box-sizing: border-box;
  text-indent: initial;
  border-spacing: 0px;
  border: 2px;
  width: 100%;
}
a
{
  color: #f1356d;
  text-decoration: none;

}
a:visited
{
  color: none;
}
a li
{
  color:#f1356d;
}

hr
{
  margin-top: 1rem;
  margin-bottom: 1rem;
  border: 0;
  border-top: 1px solid rgba(0,0,0,.1);
 
}
td
{
  vertical-align: top;
  border: solid 1px rgb(205,205,205);
}
li
{
  padding-bottom: 10px;
}
code
{
  background-color: rgb(223, 255, 228);
  border-radius: 4px;
  padding-left: 3px;
  padding-right: 3px;
  padding-bottom: 3px;
}
.Post ul
{
  margin-left: -20px;
}
.Post li
{
  margin-bottom: -8px;
}
.Post
{
  margin-right: 700px;
}
.my-single-menu
{
  background-color: rgb(255, 255, 255);
  font-size: 18px;
  box-shadow: 0 8px 6px -3px rgb(235, 235, 235);
  width: 80%;
  height: 50px;
  color: #f1356d;
  line-height: 250%;

}
.my-single-menu post-date
{
  font-size: 13px;
}
.content {
  margin-left: 250px;
  margin-right: 0px;
  padding-left: 50px;
  padding-right: 50px;
  
  height: 100%;

  padding-top: 100px;
}

.Post p
{
  margin-top: 15px;
  margin-bottom: 15px;
}
.Post img{
  max-width: 100%;
}
.sidebarLeft  
{
  position:fixed;
  background-color: rgb(250, 250, 250);
  height: 100%;
  width: 250px;
}
.sidebarLeft li:hover 
{
  background-color: #f1356d
}

.sidebarLeft a li:hover 
{
  color: white;
}
.sidebarLeft a li:focus
{
  color: rgb(63, 9, 9);
}

.sidebarLeft .avatar 
{
  margin: auto;
}

.sidebarLeft .avatar img
{
  display: block;
  margin-top: 30px;
  border-radius: 50%;
  height: 170px;
  width: 170px;
  border-color: #272727;
  border-style: solid;
  border-width: 7px;
  margin-left: auto;
  margin-right: auto;
}

.sidebarLeft ul
{
  display: block;
  padding: 0px; /* it is needed */
  padding-top: 30px;
}
.sidebarLeft li
{
  padding-left: 30px;
  padding-top: 15px;
  list-style-type: none;
  height: 30px;
}

.sidebarRight
{
  height: 100%;
  width: 400px;
  border-left: #d8d8d8 solid 1px;
  float: right;
} 

.content .sidebarRight .sidebarRightChild
{
  position: fixed;
  height: 80%;
  border-left: #d8d8d8 solid 1px;
  padding-left:  10px;
  padding-right: 10px;
  display: inline-block;
  max-width:350px;
  overflow-wrap: break-word;


}
.sidebarRight .sidebarRightChild .tags
{
  margin-top: 20px;
  display: flex;
  flex-flow: row wrap;
  /*justify-content: space-evenly;*/
}

.sidebarRight .sidebarRightChild my-tag
{
  color: rgb(255, 255, 255);
  background-color: #f1356d;
  border-radius: 8px;
  padding-left: 4px;
  padding-right: 4px;
  padding-top: 2px;
  padding-bottom: 2px;
  margin-left: 3px;
  margin-right: 3px;
  margin-top: 5px;
}

/* tablet or big screen */
@media only screen and (max-width: 1280px) 
{
  body 
  {
    background-color: rgb(255, 255, 255);
  }

  .content {
    margin-left: 100px;
    margin-right: auto;
    padding-left: 50px;
    padding-right: 50px;
    
    height: 100%;
  
    padding-top: 100px;
  }
  .Post
  {
    margin-right: 220px;
  }
  .Post img{
    max-width: 100%;
  }
  .sidebarLeft  
{
  position:fixed;
  background-color: rgb(250, 250, 250);
  height: 100%;
  width: 120px;
}
.sidebarLeft .avatar img
{
  display: block;
  margin-top: 30px;
  border-radius: 50%;
  height: 90px;
  width: 90px;
  border-color: #272727;
  border-style: solid;
  border-width: 7px;
  margin-left: auto;
  margin-right: auto;
}
.sidebarRight
{
  height: 100%;
  width: 200px;
  border-left: #d8d8d8 solid 1px;
  float: right;
} 
}

/* mobile */
@media only screen and (max-width: 1280px) 
{
  body 
  {
    background-color: rgb(167, 87, 87);
  }
}